// Where all of the links for the app occurs
// pages -> components
// services -> providers
import {
  NgModule,
  ErrorHandler
} from "@angular/core";
import {
  BrowserModule
} from "@angular/platform-browser";
import {
  IonicApp,
  IonicModule,
  IonicErrorHandler
} from "ionic-angular";
import {
  MyApp
} from "./app.component";

import {
  HttpModule
} from "@angular/http";
import {
  IonicStorageModule
} from "@ionic/storage";

import {
  AboutPage
} from "../pages/about/about";
import {
  SettingsPage
} from "../pages/settings/settings";
import {
  HomePage
} from "../pages/home/home";
import {
  TabsPage
} from "../pages/tabs/tabs";

import {
  StatusBar
} from "@ionic-native/status-bar";
import {
  SplashScreen
} from "@ionic-native/splash-screen";
import {
  WeatherProvider
} from "../providers/weather/weather";

@NgModule({
  declarations: [
    MyApp, 
    AboutPage, 
    SettingsPage, 
    HomePage, 
    TabsPage],
  imports: [
    BrowserModule, 
    HttpModule, 
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/settings/settings.module#SettingsPageModule', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] }
  ]
}), 
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp, 
    AboutPage, 
    SettingsPage, 
    HomePage, 
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {
      provide: ErrorHandler,
      useClass: IonicErrorHandler
    },
    WeatherProvider
  ]
})
export class AppModule {}



